@tailwind base;
@tailwind components;
@tailwind utilities;

*{
  scroll-behavior: smooth;
  box-sizing: border-box;
}

.slick-slide>div{
  height: 130px;
  width: 130px;
  margin-left: 70px;
  margin-bottom: 50px;
  margin-top: 50px;
  padding: 15px;
  display: flex;
  background-color: rgb(0, 0, 0);
  border-radius: 10px;
  transition: transform 0.3s ease, background-color 0.3s ease;
  
}
.slick-slide>div:hover{
  transform: scale(1.1);
}

.slick-dots {
  bottom: -30px;
  list-style: none;
  display: flex !important;
  justify-content: center;
  padding: 0;
}

.slick-dots li {
  margin: 0 5px;
}

.slick-dots li button {
  background: transparent;
  border: none;
}

.slick-dots li button:before {
  font-size: 7px;
  color: #000000;
  opacity: 0.75;
}

.slick-dots li.slick-active button:before {
  color: #3006ca;
  opacity: 1;
}
