.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal {
  background: #cfddf1;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
}

.modal-buttons {
  
  margin-top: 20px;
}

.modal-buttons>button {
  margin: 0 10px;
  background-color: rgba(0, 0, 0, 0.321);
